import React, { Component } from "react";
import Newsitems from "./Newsitems";
import ripImage from "../image/rip.jpg";
import PropTypes from "prop-types";

import "../App.css";
import Spinner from "./Spinner";
export class News extends Component {
  static defaultProps = {
    county: "in",
    category: "general",
  };
  static propTypes = {
    county: PropTypes.string,
    category: PropTypes.string,
  };
  constructor(props) {
    super(props);
    this.state = {
      News: [],
      page: 1,
      dataLoad: false,
      search: "",
      status: "",
      handlePage: "",
      date: [],
    };
    this.NextPage = this.NextPage.bind(this);
  }

  componentDidMount() {
    let url = `
    https://newsapi.org/v2/top-headlines?country=in&category=${this.props.category}&apiKey=242a67bcdf694bccbdeba380b3538a78&page=${this.state.page}`;
    // let Data = await fetch(url);
    // let NewsData = await Data.json().catch((err)=>console.log('it is falidation'));
    // console.log(NewsData);
    // this.setState({ News: NewsData.results ,status: NewsData.status});

    fetch(url)
      .then((Data) => {
        this.setState({ dataLoad: true });
        return Data.json();
      })
      .then((data) => {
        return this.setState({
          News: data.articles,
          page: this.state.page,
          totalResults: data.totalResults,
          dataLoad: false,
        });
      })
      .catch((err) => {
        console.log("sorry failed to fetch data");
      });
  }
  //use this --> this.NextPage = this.NextPage.bind(this);
  //use this --> either use arrow functions to navigate to next and previous page it's refere
  async NextPage() {
    let url = ` https://newsapi.org/v2/top-headlines?country=in&category=${
      this.props.category
    }&apiKey=242a67bcdf694bccbdeba380b3538a78&page=${this.state.page + 1}`;
    
    let Data = await fetch(url);
    let NewsData = await Data.json();
    this.setState({
      News: NewsData.articles,
      page: this.state.page + 1,
      totalResults: NewsData.totalResults,
    });
  }

  //no need to write in constructor  this --> this.NextPage = this.NextPage.bind(this);
  PreviousPage = async () => {
    let url = `https://newsapi.org/v2/top-headlines?country=in&category=${
      this.props.category
    }&apiKey=242a67bcdf694bccbdeba380b3538a78&page=${this.state.page - 1}`;
    let Data = await fetch(url);
    let NewsData = await Data.json();
    this.setState({ News: NewsData.articles, page: this.state.page - 1 });
  };
  handleChange = (event) => {
    this.setState({ search: event.target.value });
  };
  handleSubmit = async (e) => {
    e.preventDefault();

    let url = `https://newsapi.org/v2/top-headlines?country=in&category=${this.props.category}&apiKey=242a67bcdf694bccbdeba380b3538a78&page=0`;
    let Data = await fetch(url);
    this.setState({ dataLoad: true });
    let NewsData = await Data.json();
    this.setState({
      News: NewsData.articles,
      page: this.state.page,
      dataLoad: false,
    });
    if (this.state.News.length === 0) {
      this.setState({ handlePage: "No News available." });
    } else if (this.state.News.length > 0) {
      this.setState({ handlePage: "" });
    }
    console.log(this.state.News);
  };

  render() {
    return (
      <>
        <div
          className="container-fluid w-100 mx-auto "
          style={{ marginTop: "70px" }}
        >
          <div className="input-group mb-3 d-flex raw justify-content-center align-items-center"></div>
          <h2 className="text-light text-center">{this.state.handlePage}</h2>
          {this.state.dataLoad && <Spinner />}
          <div className="marquee">
            <p>This is the world Top news.</p>
          </div>

          <div className="row mx-auto">
            <h2 className="text-white text-center">Top news</h2>

            {!this.state.dataLoad &&
              this.state.News.map((ele) => {
                return (
                  <div className="col-md-3  mx-5" key={ele.url}>
                    <Newsitems
                      title={ele.title}
                      description={ele.content}
                      urlToImage={ele.urlToImage ? ele.urlToImage : ripImage}
                      url={ele.url}
                      author={ele.author ? ele.author : "Not Available"}
                      date={ele.publishedAt}
                      source={ele.source.name}
                    />
                  </div>
                );
              })}
            <div className="box d-flex gap-2 align-items-center justify-content-center">
              <button
                disabled={this.state.page <= 1}
                className="btn btn-primary"
                onClick={this.PreviousPage}
              >
                {"<<"}Previous{" "}
              </button>
              <button
                disabled={this.state.News.length <= 19}
                className="btn btn-primary"
                onClick={this.NextPage}
              >
                Next{">>"}
              </button>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default News;
